import request from "@/utils/request";

//create business
export function createBusiness(token, data) {
  return request({
    headers: {
      "Content-Type": "application/json",
      Accept: "application/json",
      Authorization: `Bearer ${token}`
    },
    url: "/api/v1/businesses",
    method: "post",
    data
  });
}

//get user businesses
export function getUserBusinesses(token, id) {
  return request({
    headers: {
      "Content-Type": "application/json",
      Accept: "application/json",
      Authorization: `Bearer ${token}`
    },
    url: `/api/v1/users/${id}/businesses`,
    method: "get"
  });
}
